import { MDBRow, MDBCol } from 'mdbreact';
import React from 'react';
import Microphone from '../assets/microphone.png';
import Overview from '../assets/overview.jpg';

const abstractText = "Automatic lyric transcription (ALT) is a nascent field of study attracting increasing interest from both the speech and music information retrieval communities, given its significant application potential. However, ALT with audio data alone is a notoriously difficult task due to instrumental accompaniment and musical constraints resulting in degradation of phonetic cues and intelligibility of sung lyrics. To tackle this challenge, we propose MM-ALT, a multimodal automatic lyric transcription system, together with a new dataset, N20EM, consisting of audio recording, video of lip movement, and inertial measurement unit (IMU) data of an earbud worn by the performing singer. We first adapt the wav2vec 2.0 framework from automatic speech recognition (ASR) to the ALT task. We then propose a lipreading-based ALT method, an IMU-based voice activity detection (VAD) method, as well as the Residual Cross Attention (RCA) mechanism to fuse data from the three modalities (i.e. audio, video, and IMU). Experiments show the effectiveness of our proposed MM-ALT system, especially its superior performance of noise robustness."

class Landing extends React.Component {

  state = {

  }

  componentDidMount = async () => {
  }

  render() {

    const styles = {
      landing: {
        padding: '10vh 15vw 5vh 15vw'
      },
    };

    return (
      <div style={styles.landing}>
        <MDBRow>

          <MDBCol md="6">
            <MDBCol md="12">
              <div className="header">
                {"A Multimodal Automatic Lyric Transcription System in English"}
              </div>

              <div style={{ padding: '2vh 0vh' }} className="credits">
                {"Gu Xiangming, Ou Longshen, Danielle Ong, Fadi Sng, Wang Ye"}
              </div>
            </MDBCol>
          </MDBCol>

          <MDBCol style={{ display: 'flex', justifyContent: 'flex-end' }} md="6">
            <img
              src={Microphone}
              alt=''
              style={{height: '35vh'}}
            />
          </MDBCol>

        </MDBRow>

        <MDBRow style={{ marginTop: '2vh', marginLeft: '3px' }}>
          <MDBCol md="12">
            <div className="sectionHeader">
              {"Abstract"}
            </div>
          </MDBCol>

          <MDBCol style={{ marginTop: '1vh' }} md="12">
            <div className="textDescription">
              {abstractText}
            </div>
          </MDBCol>
        </MDBRow>

        <MDBRow style={{ margin: '5vh 0vh 5vh 3px' }}>
          <MDBCol md="12">
            <div className="sectionHeader">
              {"Overview"}
            </div>
          </MDBCol>

          <MDBCol md="12">
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <img
                src={Overview}
                alt=''
                style={{ width: '90%', padding: '3vh 0vh' }}
              />
            </div>
          </MDBCol>

          <MDBCol style={{ marginTop: '1vh' }} md="12">
            <div className="textDescription">
              {"We first build a multimodal lyric transcription system, MM-ALT, which utilizes three modalities of input: audio, video, and signals from wearable IMU sensors. To facilitate building the system, we propose the N20EM dataset for multimodal lyric transcription. We create a group of baseline models on this dataset that can perform multimodal lyric transcription with varying combinations of modalities, obtaining a minimum word error rate (WER) of 12.71%. We further reveal an increase in system robustness by introducing additional modalities. With severe perturbations of musical accompaniments, our system can achieve 27.04% lower WER compared to its audio-only counterparts."}
            </div>

            <div className="textDescription" style={{ marginTop: '2vh' }}>
              {"To our knowledge, this is a pioneer attempt at building a frame-level VAD system solely from motion data captured by a wearable IMU device. Our experiments elucidate the correlation between the IMU and audio modalities. "}
            </div>

            <div className="textDescription" style={{ marginTop: '2vh' }}>
              {"We propose Residual Cross Attention, a new feature fusion method to deal with the unbalanced contributions of different modalities. We demonstrate the effectiveness of this new modality fusion method in our ALT system by comparing it with various feature fusion methods."}
            </div>

            <div className="textDescription" style={{ marginTop: '2vh' }}>
              {"We initialize a new task referred to as lyric lipreading. In this task, we attempt to recognize lyrics in singing utilizing only video information. This is the first attempt to retrieve language-related information from singing recordings without the help of audio input."}
            </div>
          </MDBCol>
        </MDBRow>
      </div>
    );
  }
}

export default Landing;
